import { createActions } from 'redux-actions';

export const { getTokenInfo, getPool, updateTokenPrice, updateTokenOldPrice } =
  createActions(
    'getTokenInfo',
    'getPool',
    'updateTokenPrice',
    'updateTokenOldPrice',
    { prefix: 'TOKEN' }
  );

const action = {
  getTokenInfo,
  getPool,
  updateTokenPrice,
  updateTokenOldPrice,
};

export default action;
