import _ from 'lodash';
import tokenHelper from '../helper/tokenHelper';
import { ITokenDetail, ITokenTable } from '../model/tokenModel';

export function mappingTokenTable(tokens: ITokenDetail[]): ITokenTable[] {
  const mappedTokenTable: ITokenTable[] = _.chain(tokens)
    .sortBy(['variationPrice'])
    .map(
      (token: ITokenDetail): ITokenTable => ({
        coinTitle: `${
          _.isNil(token.burn)
            ? token.name
            : '*[' +
              tokenHelper.buildBurnPercent(token.burn) +
              '] ' +
              token.name
        } (${token.symbol ? token.symbol : 'NA'})`,
        pool: _.isNil(token.targetPool?.liquidity)
          ? 'NA'
          : `${tokenHelper.convertToInternationalCurrencySystem(
              token.targetPool?.liquidity as number
            )} (${token.targetPool?.dApp} | ${token.targetPool?.quoteSymbol})`,
        price: _.isNil(token.price)
          ? 'NA'
          : `${tokenHelper.roundCurrency(token.price)}`,
        variation: token.variationPrice as number,
        address: token.address,
        logoURI: token.logoURI,
        web: token.web,
      })
    )
    .value();

  return mappedTokenTable;
}

export function sortTopMoverTokens(tokens: ITokenTable[]): ITokenTable[] {
  const topDownMovers: ITokenTable[] = _.slice(tokens, 0, 5);
  const topUpMovers: ITokenTable[] = _.slice(tokens, -5);
  const topMoverTokens: ITokenTable[] = _.uniqBy(
    [...topDownMovers, ...topUpMovers, ..._.slice(tokens, 5, -5)],
    'address'
  );

  return topMoverTokens;
}
