import _ from 'lodash';
import { handleActions } from 'redux-actions';
import {
  MIN_LIQUIDITY,
  TIME_CACHE_TOKEN_PRICE,
  TIME_RESET_NOTIFICATION,
  LOSER_PERCENT_NOTIFY,
  LOSER_PERCENT_NOTIFY_UP,
  TIME_CACHE_TOKEN_PRICE_FOMO,
  TIME_RESET_NOTIFICATION_FOMO,
  UP_PERCENT_NOTIFY_FOMO,
} from '../config/appConfig';
import { TOKEN_BEP20_TRACKING } from '../config/tokenBep20TrackingConfig';
import { TOKEN_BEP20_DISABLE } from '../config/tokenBep20DisableConfig';
import {
  getTokenInfo,
  getPool,
  updateTokenPrice,
  updateTokenOldPrice,
} from './tokenActionCreater';
import {
  ITokenBase,
  ITokenDetail,
  CachePrice,
  TimelinePrice,
  IVariationNotification,
} from '../model/tokenModel';

const tokenBep20Disable: string[] = TOKEN_BEP20_DISABLE.map(
  (address: string): string => address.toLowerCase()
);
const initialState: ITokenDetail[] = _.chain(TOKEN_BEP20_TRACKING)
  .map(
    (token: ITokenBase): ITokenBase => ({
      ...token,
      address: token.address.toLowerCase(),
    })
  )
  .filter(
    (token: ITokenBase): boolean => !tokenBep20Disable.includes(token.address)
  )
  .uniqBy('address')
  .value();
// console.log(initialState);

export default handleActions(
  {
    [getTokenInfo as any]: (state: ITokenDetail[], action): ITokenDetail[] => {
      return action.payload;
    },

    [getPool as any]: (state: ITokenDetail[], action): ITokenDetail[] => {
      // Check tokens are low lp or high burn rate!
      const tokenLowLp: string[] = action.payload
        .filter((token: ITokenDetail): boolean => {
          return (
            (token.targetPool?.liquidity || 0) < 100000 ||
            (token.burn || 0) > 10 ||
            ((token.burn || 0) !== 0 &&
              (token.targetPool?.liquidity || 0) < 150000) ||
            ((token.burn || 0) > 5 &&
              (token.targetPool?.liquidity || 0) < 300000)
          );
        })
        .map((token: ITokenDetail): string => token.address);
      console.log(tokenLowLp);

      return action.payload.filter((token: ITokenDetail): boolean => {
        return (token.targetPool?.liquidity || 0) >= MIN_LIQUIDITY;
      });
    },

    [updateTokenPrice as any]: (
      state: ITokenDetail[],
      action
    ): ITokenDetail[] => {
      const newState: ITokenDetail[] = state.map(
        (token: ITokenDetail): ITokenDetail => {
          const matchToken = _.find(action.payload, { address: token.address });

          if (matchToken) {
            const cachePrice: CachePrice = computedCachePrice(
              matchToken.cachePrice as CachePrice,
              matchToken.price as number
            );
            const cachePriceFomo: CachePrice = computedCachePriceFomo(
              matchToken.cachePrice as CachePrice,
              matchToken.price as number
            );
            const variationPrice: number = computedVariationPrice(
              matchToken.price as number,
              cachePrice.price
            );
            const variationPriceFomo: number = computedVariationPrice(
              matchToken.price as number,
              cachePriceFomo.price
            );
            const variationNotification: IVariationNotification =
              computedVariationNotification(
                variationPrice,
                token.variationNotification as IVariationNotification
              );
            const variationNotificationFomo: IVariationNotification =
              computedNotificationFomo(
                variationPriceFomo,
                token.variationNotificationFomo as IVariationNotification
              );

            return {
              ...token,
              price: matchToken.price,
              variationPrice,
              targetPool: matchToken.targetPool,
              cachePrice,
              variationNotification,
              variationNotificationFomo,
            };
          }

          return token;
        }
      );

      return newState;
    },

    [updateTokenOldPrice as any]: (
      state: ITokenDetail[],
      action
    ): ITokenDetail[] => {
      const newState: ITokenDetail[] = state.map(
        (token: ITokenDetail): ITokenDetail => {
          const matchToken = _.find(action.payload, { address: token.address });

          if (matchToken) {
            return {
              ...token,
              oldPrice: matchToken.oldPrice,
            };
          }

          return token;
        }
      );

      return newState;
    },
  },
  initialState
);

function computedCachePrice(cachePrice: CachePrice, price: number): CachePrice {
  let newCachePrice: CachePrice;
  const newTimelinePrice = {
    price,
    timestamp: +new Date(),
  };

  if (cachePrice) {
    let timelinePrices: TimelinePrice[] = [
      ...cachePrice.timelinePrices,
      newTimelinePrice,
    ];

    if (
      newTimelinePrice.timestamp - timelinePrices[0].timestamp >
      TIME_CACHE_TOKEN_PRICE
    ) {
      timelinePrices = timelinePrices.slice(1, timelinePrices.length);
    }

    newCachePrice = { price: timelinePrices[0].price, timelinePrices };
  } else {
    newCachePrice = { price, timelinePrices: [newTimelinePrice] };
  }

  return newCachePrice;
}

function computedCachePriceFomo(
  cachePrice: CachePrice,
  price: number
): CachePrice {
  let newCachePrice: CachePrice;
  const newTimelinePrice = {
    price,
    timestamp: +new Date(),
  };

  if (cachePrice) {
    let timelinePrices: TimelinePrice[] = [
      ...cachePrice.timelinePrices,
      newTimelinePrice,
    ];

    if (
      newTimelinePrice.timestamp - timelinePrices[0].timestamp >
      TIME_CACHE_TOKEN_PRICE_FOMO
    ) {
      timelinePrices = timelinePrices.slice(1, timelinePrices.length);
    }

    newCachePrice = { price: timelinePrices[0].price, timelinePrices };
  } else {
    newCachePrice = { price, timelinePrices: [newTimelinePrice] };
  }

  return newCachePrice;
}

function computedVariationPrice(price: number, cachePrice: number): number {
  if (!price || !cachePrice) {
    return 0;
  }

  const variationPrice: number = ((price - cachePrice) / cachePrice) * 100;

  return variationPrice;
}

function computedVariationNotification(
  variation: number,
  prevNotification: IVariationNotification
): IVariationNotification {
  let notification: IVariationNotification = {
    isPush: false,
    hasOncePush: false,
    timestamp: +new Date(2100, 1, 1),
  };

  if (
    !prevNotification ||
    +new Date() - prevNotification.timestamp >= TIME_RESET_NOTIFICATION
  ) {
    if (variation <= -LOSER_PERCENT_NOTIFY) {
      notification = {
        cacheVariation: variation,
        isPush: true,
        hasOncePush: true,
        timestamp: +new Date(),
      };
    }
  } else {
    if (prevNotification.hasOncePush) {
      if (
        variation <=
        (prevNotification.cacheVariation as number) - LOSER_PERCENT_NOTIFY_UP
      ) {
        notification = {
          ...prevNotification,
          cacheVariation: variation,
          isPush: true,
        };
      } else {
        notification = {
          ...prevNotification,
          isPush: false,
        };
      }
    } else {
      if (variation <= -LOSER_PERCENT_NOTIFY) {
        notification = {
          cacheVariation: variation,
          isPush: true,
          hasOncePush: true,
          timestamp: +new Date(),
        };
      }
    }
  }

  return notification;
}

function computedNotificationFomo(
  variation: number,
  prevNotification: IVariationNotification
): IVariationNotification {
  let notification: IVariationNotification = {
    isPush: false,
    hasOncePush: false,
    timestamp: +new Date(2100, 1, 1),
  };

  if (
    !prevNotification ||
    +new Date() - prevNotification.timestamp >= TIME_RESET_NOTIFICATION_FOMO
  ) {
    if (variation >= UP_PERCENT_NOTIFY_FOMO) {
      notification = {
        cacheVariation: variation,
        isPush: true,
        hasOncePush: true,
        timestamp: +new Date(),
      };
    }
  } else {
    if (prevNotification.hasOncePush) {
      notification = {
        ...prevNotification,
        isPush: false,
      };
    } else {
      if (variation >= UP_PERCENT_NOTIFY_FOMO) {
        notification = {
          cacheVariation: variation,
          isPush: true,
          hasOncePush: true,
          timestamp: +new Date(),
        };
      }
    }
  }

  return notification;
}
