import { handleActions } from 'redux-actions';
import { BNB_BEP20 } from '../config/tokenBep20Config';
import { BNB_BUSD_PANCAKE_POOL } from '../config/web3Config';
import { updateBnb } from './quoteTokenActionCreater';

export type IQuoteToken = {
  bnb: {
    symbol: string;
    poolAddress: string;
    price?: number;
  };
};

const initialState: IQuoteToken = {
  bnb: {
    symbol: BNB_BEP20.symbol as string,
    poolAddress: BNB_BUSD_PANCAKE_POOL,
  },
};

export default handleActions(
  {
    [updateBnb as any]: (state: IQuoteToken, action): IQuoteToken => ({
      ...state,
      bnb: {
        ...state.bnb,
        price: action.payload.bnb.price,
      },
    }),
  },
  initialState
);
