import { PureComponent, ReactElement } from 'react';
import { Container } from 'reactstrap';
import { getTokenInfos } from '../service/tokenInfoService';
import { getPools } from '../service/tokenPoolService';
import { updateTokenPrice } from '../service/tokenPriceService';
import { checkBrowserNotification } from '../service/notificationService';
import TokenTable from './TokenTable/TokenTable';

class App extends PureComponent {
  async componentDidMount() {
    await getTokenInfos();
    await getPools();

    this.recursionUpdateTokenPrice();

    checkBrowserNotification();
  }

  private async recursionUpdateTokenPrice() {
    await updateTokenPrice();
    await this.recursionUpdateTokenPrice();
  }

  render(): ReactElement {
    return (
      <Container fluid='lg' className='py-2 py-lg-4 py-xl-5'>
        <TokenTable />
      </Container>
    );
  }
}

export default App;
